function clientNameMapping(clientCode) {
  switch (clientCode) {
    case 'gd':
      return 'Glasses Direct';
    default:
      return '';
  }
}

function fraudCheckStatus(statusVal) {
  var testVal = statusVal.toUpperCase();
  if (testVal.indexOf('PASSED') > -1) {
    return 'passed';
  }
  if (testVal.indexOf('MAYBE') > -1) {
    return 'maybe';
  }
  if (testVal.indexOf('FAILED') > -1) {
    return 'failed';
  }
}

function currency(currencyCode) {
  switch (currencyCode) {
    case 'GBP':
      return '';
    default:
      return '';
  }
}

function getStatusClass(statusVal) {
  console.log(statusVal);
  var testVal = statusVal.toUpperCase();
  switch (testVal) {
    case 'COMPLETE':
      return 'status-complete';
    case 'PAYMENT RECEIVED':
      return 'status-paid';
    case 'DISPATCHED':
      return 'status-dispatched';
    case 'IN LAB':
      return 'status-lab';
    default:
      return '';
  }
}

